import React from 'react';
import {MDBBtn} from 'mdbreact';

const Album_modal_cameraSecondBtn = (props) => {

    return (
        <React.Fragment>
            <div>
                <MDBBtn type="submit" name="save" color="success"></MDBBtn>
                <MDBBtn name="retake" color="warning" onClick={props.onClickRetake}></MDBBtn>
            </div>
        </React.Fragment>
    )
}
export default Album_modal_cameraSecondBtn;
