
export const MEMORIALDAY_REQUEST = 'MEMORIALDAY_REQUEST';
export const MEMORIALDAY_FAIL = 'MEMORIALDAY_FAIL';
export const MEMORIALDAY_SUCCESS = 'MEMORIALDAY_SUCCESS';

export const MEMORIALDAY_OUT = 'MEMORIALDAY_OUT';

export const getUserMemorialDay = () => ({ type: MEMORIALDAY_REQUEST, data:null});
export const memorialOut = () => ({ type: MEMORIALDAY_OUT,data:null});

const initialState = {
      Title:{
        title:"",
        back:"Back",
        update:"Update",
        backUrl:"./main",
        updateUrl:"./memorialUpdate",
        icon:{main:"american-sign-language-interpreting fa-3x", update:"pen-nib fa-2x", back:"arrow-circle-left fa-2x"}
      },
      MemorialBody:{
        firstSpace:{firstSpaceDay:"1200",firstSpaceLeftDay:"89 "},
        secondSpace:{secondSpaceDay:"1300",secondSpaceLeftDay:"189 "},
        thirdSpace:{thirdSpaceDay:"1400",thirdSpaceLeftDay:"289 "},
        forthSpace:{forthSpaceDay:"4",forthSpaceLeftDay:"351 "},
        firstDay:{firstDay:" ",SeeingDay:"1111 "},
        guy:{name:" ", birthday:"76 ",},
        girl:{name:" ", birthday:"57 ",}
      },
      mainState:'isReady',
      comment:'',
      errMessage:null,
  };


export default function reducer(state = initialState, action) {
   
    switch(action.type) {
        
        case MEMORIALDAY_REQUEST:
            return {
                ...state,
                comment:' ....',
                mainState:'isReady',
            };
    
          case MEMORIALDAY_SUCCESS:
            const {name,oppentname,birth,oppenetbirthday,
                    relDay} = action.data.user_info;
             const {first,second,third,forth,
                    } = action.data.user_info.calDay;        
            return (
              {...state,
                MemorialBody:{
                  firstSpace:{...state.MemorialBody.firstSpace ,firstSpaceLeftDay:`${first} `},
                  secondSpace:{...state.MemorialBody.secondSpace, secondSpaceLeftDay:`${second} `},
                  thirdSpace:{...state.MemorialBody.thirdSpace, thirdSpaceLeftDay:`${third} `},
                  forthSpace:{...state.MemorialBody.forthSpace, forthSpaceLeftDay:`${forth} `},
                  firstDay:{...state.MemorialBody.firstDay ,SeeingDay:`${relDay} `},
                  guy:{name:`${name} `, birthday:`${birth}`},
                  girl:{name:`${oppentname} `, birthday:`${oppenetbirthday}`,}
                },
                mainState:'isSuccess'
              }
            );
    
          case MEMORIALDAY_FAIL:
            return {
              ...state,
              mainState:'isFail',
              errMessage:action.error,
            };

          case MEMORIALDAY_OUT:
              return {
                ...state,
                mainState:'isReady',
              }; 
      default:
        return state; 
    }
  }
